import * as React from 'react';

interface IProps {
  title: string,
  onPress: () => void,
  className?: string,
  style? : {}
};

export default (props: IProps) => {
  const {title, onPress, className, style} = props;
  return (
    <input type='button' className={'btn' + (className ? className : ' btn-primary')} value={title} onClick={() => onPress()} style={{...style}} />
  );
}