import * as React from 'react';
import { View } from '../../common/elements';
import { ROLE } from '../../../constants';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { IBlocks } from '../../../models/store/blocks';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  blocks: IBlocks
};

export default (props: IProps) => {
  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item w-100">
          <View className="row gap-20">
            <View className='col-md-3'>
              <View className="layers bd bgc-white p-20 cur-p" onClick={() => props.history.push('tickets')}>
                <View className="layer w-100 mB-10">
                  <h6 className="lh-1">Ticket(s)</h6>
                </View>
                <View className="layer w-100">
                  <View className="peers ai-sb fxw-nw">
                    <View className="peer peer-greed">
                      <span className="icon-holder">
                        <i className='c-orange-500 ti-headphone-alt fa-2x'></i>
                      </span>
                    </View>
                    <View className="peer">
                    <span className="d-ib lh-0 va-m c-orange-500">View all</span>
                    </View>
                  </View>
                </View>
              </View>
            </View>
            <View className='col-md-3'>
              <View className="layers bd bgc-white p-20 cur-p" onClick={() => props.history.push('ads')}>
                <View className="layer w-100 mB-10">
                    <h6 className="lh-1">ads</h6>
                  </View>
                <View className="layer w-100">
                  <View className="peers ai-sb fxw-nw">
                    <View className="peer peer-greed">
                      <span className="icon-holder">
                        <i className='c-teal-500 fa fa-bullhorn fa-2x'></i>
                      </span>
                    </View>
                    <View className="peer">
                      <span className="d-ib lh-0 va-m c-teal-500">View all</span>
                    </View>
                  </View>
                </View>
              </View>
            </View>
            <View className='col-md-3'>
              <View className="layers bd bgc-white p-20 cur-p" onClick={() => props.history.push('notification')}>
                <View className="layer w-100 mB-10">
                  <h6 className="lh-1">Notification</h6>
                </View>
                <View className="layer w-100">
                  <View className="peers ai-sb fxw-nw">
                    <View className="peer peer-greed">
                      <span className="icon-holder">
                        <i className='c-red-500 ti-bell fa-2x'></i>
                      </span>
                    </View>
                    <View className="peer">
                      <span className="d-ib lh-0 va-m c-red-500">View all</span>
                    </View>
                  </View>
                </View>
              </View>
            </View>
            <View className='col-md-3'>
              <View className="layers bd bgc-white p-20 cur-p" onClick={() => props.history.push('all-visitors')}>
                <View className="layer w-100 mB-10">
                  <h6 className="lh-1">Visitor(s)</h6>
                </View>
                <View className="layer w-100">
                  <View className="peers ai-sb fxw-nw">
                    <View className="peer peer-greed">
                      <span className="icon-holder">
                        <i className='c-pink-500 fa fa-id-card-o fa-2x'></i>
                      </span>
                    </View>
                    <View className="peer">
                      <span className="d-ib lh-0 va-m c-pink-500">View all</span>
                    </View>
                  </View>
                </View>
              </View>
            </View>
          </View>
          <View className="row gap-20">
            {props.appUser.role === ROLE.ADMIN &&
            <View className='col-md-3'>
              <View className="layers bd bgc-white p-20 cur-p" onClick={() => props.history.push('users')}>
                <View className="layer w-100 mB-10">
                  <h6 className="lh-1">User(s)</h6>
                </View>
                <View className="layer w-100">
                  <View className="peers ai-sb fxw-nw">
                    <View className="peer peer-greed">
                      <span className="icon-holder">
                        <i className='c-deep-purple-500 fa fa-users fa-2x'></i>
                      </span>
                    </View>
                    <View className="peer">
                      <span className="d-ib lh-0 va-m fw-600 bdrs-10em pX-15 pY-15 bgc-deep-purple-50 c-deep-purple-500">
                      {props.users.isUpToDate ?
                      (!props.users.isError) ? props.users.data.length : 0
                      :
                      "Loading"
                      }
                      </span>
                    </View>
                  </View>
                </View>
              </View>
            </View>
            }
            {props.appUser.role === ROLE.ADMIN &&
            <View className='col-md-3'>
              <View className="layers bd bgc-white p-20 cur-p" onClick={() => props.history.push('homes')}>
                <View className="layer w-100 mB-10">
                  <h6 className="lh-1">Home(s)</h6>
                </View>
                <View className="layer w-100">
                  <View className="peers ai-sb fxw-nw">
                    <View className="peer peer-greed">
                      <span className="icon-holder">
                        <i className='c-blue-700 fa fa-home fa-2x'></i>
                      </span>
                    </View>
                    <View className="peer">
                      <span className="d-ib lh-0 va-m fw-600 bdrs-10em pX-15 pY-15 bgc-blue-50 c-blue-700">
                      {props.homes.isUpToDate ?
                      (!props.homes.isError) ? props.homes.data.length : 0
                      :
                      "Loading"
                      }
                      </span>
                    </View>
                  </View>
                </View>
              </View>
            </View>
            }
            {props.appUser.role === ROLE.ADMIN &&
            <View className='col-md-3'>
              <View className="layers bd bgc-white p-20 cur-p" onClick={() => props.history.push('blocks')}>
                <View className="layer w-100 mB-10">
                  <h6 className="lh-1">Block(s)</h6>
                </View>
                <View className="layer w-100">
                  <View className="peers ai-sb fxw-nw">
                    <View className="peer peer-greed">
                      <span className="icon-holder">
                        <i className='c-brown-500 fa fa-building fa-2x'></i>
                      </span>
                    </View>
                    <View className="peer">
                      <span className="d-ib lh-0 va-m fw-600 bdrs-10em pX-15 pY-15 bgc-brown-50 c-brown-500">
                      {props.blocks.isUpToDate ?
                      (!props.blocks.isError) ? props.blocks.data.length : 0
                      :
                      "Loading"
                      }
                      </span>
                    </View>
                  </View>
                </View>
              </View>
            </View>
            }
            <View className='col-md-3'>
              <View className="layers bd bgc-white p-20 cur-p" onClick={() => props.history.push('payment')}>
                <View className="layer w-100 mB-10">
                  <h6 className="lh-1">Payment</h6>
                </View>
                <View className="layer w-100">
                  <View className="peers ai-sb fxw-nw">
                    <View className="peer peer-greed">
                      <span className="icon-holder">
                        <i className='c-indigo-500 fa fa-credit-card fa-2x'></i>
                      </span>
                    </View>
                    <View className="peer">
                      <span className="d-ib lh-0 va-m c-indigo-500">View all</span>
                    </View>
                  </View>
                </View>
              </View>
            </View>
          </View>
        </View>
        
      </View>
    </View>
  );
};