import * as React from 'react';
import { View, Text } from '../common/elements';
import Breadcrum from '../common/breadcrum';
import { styles as modelStyles } from "../../styles/model";
import { COLOR } from '../../constants';
import { IUser } from '../../models/user';

interface IProps {
  user: IUser
};

export default (props: IProps) => {
  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Info', link: 'info'}]} />
          <View className='bgc-white p-20'>
          
          {/* <Image source={Images.workInProgress} style={{width: 50, height: 45, marginTop: 35}} />
              <Text>Work in progress.</Text> */}
          <View className='center' style={modelStyles.row}>
            <Text style={{color: COLOR.SECONDARY, fontWeight: 'bold', textAlign: 'center'}}>{props.user.client.name}</Text>
          </View>
          <View className='center' style={modelStyles.row}>
              <Text style={{color: COLOR.SECONDARY, fontWeight: 'bold', textAlign: 'center'}}>{props.user.client.description}</Text>
          </View>
          <View style={modelStyles.row}>
            <Text>
              {"Apartment Residents & Apartment Owners need a common platform through which they can connect with association, get to know & discuss common issues of the neighborhood"}
            </Text>
          </View>
          <View style={modelStyles.row}>
            <Text>Home users</Text>
          </View>
          <View style={modelStyles.row}>
            <Text style={styles.listItem}>Maintenance Admin Payment</Text>
          </View>
          <View style={modelStyles.row}>
            <Text style={styles.listItem}>Receive real-time notifications</Text>
          </View>
          <View style={modelStyles.row}>
            <Text style={styles.listItem}>Visitor management</Text>
          </View>
          <View style={modelStyles.row}>
            <Text style={styles.listItem}>Lodge any complaint related to your apartment and track</Text>
          </View>
          <View style={modelStyles.row}>
            <Text>Admin Users</Text>
          </View>
          <View style={modelStyles.row}>
            <Text style={styles.listItem}>Create polls and gather the opinion of all Apartment residents</Text>
          </View>
          <View style={modelStyles.row}>
            <Text style={styles.listItem}>All details of your flat, staff who work in your flat, visitors coming to your flat</Text>
          </View>
          <View style={modelStyles.row}>
            <Text style={styles.listItem}>Accounts  and much more...</Text>
          </View>
        </View>
      </View>
      </View>
    </View>
  );
};

const styles = {
  content: {
    flex: 1,
    // backgroundColor: 'transparent',
    width: '100%'
  },
  listItem: {
    marginLeft: 15
  }
};