import * as React from 'react';
import {  View } from '../../common/elements';
import Breadcrum from '../../common/breadcrum';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IHomes } from '../../../models/store/homes';
import { IPayment } from '../../../models/home';
import { COLOR } from '../../../constants';
import PaymentForm from './payment-form';
import PaymentHistory from './payment-history';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  onLoad: (appClientId: string, homeId: string, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => void,
  onSave: (appClientId: string, data: any, user: IUser, callback: (result: boolean, message: string) => void) => void,
  onSearch: (appClientId: string, data: any, user: IUser, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => void,
  onUpdate: (id: string, data: any, user: IUser, callback: (result: boolean, message: string) => void) => void
};

interface IState {
  activeTab: number,
  history: Array<IPayment>
};

export default class Payment extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      activeTab: 1,
      history: []
    }
  };
  
  componentDidMount(){
    /*
    this.props.onLoad(this.props.user.client._id, '', (result: Array<IPayment>) => {
      if(isNN(result)){
        this.setState({
          history: result
        });
      }
    }, (message: string) => {
      Toast.fail(message ? message : 'Failed to load Payments.');
    });
    */
  };
  
  componentWillUnmount(){};

  render() {
    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Payment', link: 'payment'}]} />
        <View className='bgc-white p-20'>
          <View className='tab-bar' style={{flex: 1, flexDirection: 'row'}}>
            <span className={'tab' + (this.state.activeTab === 1 ? ' active': '')} onClick={() => this.setState({activeTab: 1})}>
              NEW
            </span>
            <span className={'tab' + (this.state.activeTab === 2 ? ' active': '')} onClick={() => this.setState({activeTab: 2})}>
              HISTORY
            </span>
          </View>
          <View className='tab-content p-15'>
            {
              this.state.activeTab === 1 ? 
                <PaymentForm history={this.props.history} appUser={this.props.appUser} user={this.props.user} homes={this.props.homes} onSave={this.props.onSave} />
              :
                <PaymentHistory history={this.props.history} appUser={this.props.appUser} user={this.props.user} homes={this.props.homes} onSearch={this.props.onSearch} onUpdate={this.props.onUpdate} />
            }            
          </View>
        </View>        
      </View>
      </View>
      </View>
    );
  }
};