import * as React from 'react';
import { View } from '../common/elements';
import { IUser } from '../../models/user/user';
import { IHome } from '../../models/home/home';
import { ITransaction } from '../../models/home/transaction';
import MyDues from './my-dues';
import History from './history';
import CustomList from './custom-list';
import { COLOR } from '../../constants';

interface IProps {
  history: any,
  user: IUser,
  home: IHome
  onPaymentUpload: (file: any, homeId: string, callback: (result: boolean) => void) => void,
  onPaymentUpdate: (paymentId: string, transaction: ITransaction, callback: (result: boolean) => void) => void
};

interface IState{
  activeTab: number
};

export default class Home extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      activeTab: 1
    }
  };

  componentWillUnmount() {};
  
  render() {
    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item w-100">
            <View className="row gap-20">
              <View className='col-md-3'>
                <View className="layers bd bgc-white p-20 cur-p" onClick={() => this.props.history.push('members')}>
                  <View className="layer w-100 mB-10">
                    <h6 className="lh-1">Member(s)</h6>
                  </View>
                  <View className="layer w-100">
                    <View className="peers ai-sb fxw-nw">
                      <View className="peer peer-greed">
                        <span className="icon-holder">
                          <i className='c-brown-500 fa fa-users fa-2x'></i>
                        </span>
                      </View>
                      <View className="peer">
                        <span className="d-ib lh-0 va-m fw-600 bdrs-10em pX-15 pY-15 bgc-brown-50 c-brown-500">
                          {this.props.home.members ? this.props.home.members.length : 0}
                        </span>
                      </View>
                    </View>
                  </View>
                </View>
              </View>
              <View className='col-md-3'>
                <View className="layers bd bgc-white p-20 cur-p" onClick={() => this.props.history.push('vehicles')}>
                  <View className="layer w-100 mB-10">
                    <h6 className="lh-1">Vehicle(s)</h6>
                  </View>
                  <View className="layer w-100">
                    <View className="peers ai-sb fxw-nw">
                      <View className="peer peer-greed">
                        <span className="icon-holder">
                          <i className='c-indigo-500 fa fa-car fa-2x'></i>
                        </span>
                      </View>
                      <View className="peer">
                        <span className="d-ib lh-0 va-m fw-600 bdrs-10em pX-15 pY-15 bgc-indigo-50 c-indigo-500">
                          {this.props.home.vehicles ? this.props.home.vehicles.length : 0}
                        </span>
                      </View>
                    </View>
                  </View>
                </View>
              </View>
              <View className='col-md-3'>
                <View className="layers bd bgc-white p-20 cur-p" onClick={() => this.props.history.push('servants')}>
                  <View className="layer w-100 mB-10">
                    <h6 className="lh-1">Servant(s)</h6>
                  </View>
                  <View className="layer w-100">
                    <View className="peers ai-sb fxw-nw">
                      <View className="peer peer-greed">
                        <span className="icon-holder">
                          <i className='c-deep-purple-500 fa fa-female fa-2x'></i>
                        </span>
                      </View>
                      <View className="peer">
                        <span className="d-ib lh-0 va-m c-deep-purple-500">View all</span>
                      </View>
                    </View>
                  </View>
                </View>
              </View>
              <View className='col-md-3'>
                <View className="layers bd bgc-white p-20 cur-p" onClick={() => this.props.history.push('tickets')}>
                  <View className="layer w-100 mB-10">
                    <h6 className="lh-1">Ticket(s)</h6>
                  </View>
                  <View className="layer w-100">
                    <View className="peers ai-sb fxw-nw">
                      <View className="peer peer-greed">
                        <span className="icon-holder">
                          <i className='c-orange-500 ti-headphone-alt fa-2x'></i>
                        </span>
                      </View>
                      <View className="peer">
                        <span className="d-ib lh-0 va-m c-orange-500">View all</span>
                      </View>
                    </View>
                  </View>
                </View>
              </View>
            </View>
            <View className="row gap-20">
              <View className='col-md-3'>
                <View className="layers bd bgc-white p-20 cur-p" onClick={() => this.props.history.push('visitors')}>
                  <View className="layer w-100 mB-10">
                    <h6 className="lh-1">Visitor(s)</h6>
                  </View>
                  <View className="layer w-100">
                    <View className="peers ai-sb fxw-nw">
                      <View className="peer peer-greed">
                        <span className="icon-holder">
                          <i className='c-pink-500 fa fa-id-card-o fa-2x'></i>
                        </span>
                      </View>
                      <View className="peer">
                        <span className="d-ib lh-0 va-m c-pink-500">View all</span>
                      </View>
                    </View>
                  </View>
                </View>
              </View>
            </View>
          </View>

          <View className="masonry-item col-12">
            <View className='tab-bar bgc-white p-20'>
              <span className={'tab' + (this.state.activeTab === 1 ? ' active': '')} onClick={() => this.setState({activeTab: 1})}>
                MY DUES
                </span>
              <span className={'tab' + (this.state.activeTab === 2 ? ' active': '')} onClick={() => this.setState({activeTab: 2})}>
                ADVANCE
              </span>
              <span className={'tab' + (this.state.activeTab === 3 ? ' active' : '')} onClick={() => this.setState({activeTab: 3})}>
                HISTORY
              </span>
              {/* <span className={'tab' + (this.state.activeTab === 4 ? ' active' : ')} onClick={() => this.setState({activeTab: 4})}>
                DEPOSITS
              </span> */}
            </View>
            <View className='bgc-white p-20'>
            {this.state.activeTab === 1 ?
                <MyDues history={this.props.history} user={this.props.user} home={this.props.home} onPaymentUpload={this.props.onPaymentUpload} onPaymentUpdate={this.props.onPaymentUpdate} />
              :
              this.state.activeTab === 2 ?
                <CustomList data={[]} message='No advance found!' />
                :
                this.state.activeTab === 3 ?
                <History history={this.props.history} home={this.props.home} />
                :
                this.state.activeTab === 4 ?
                <CustomList data={[]} message="Work in progress" />
                :
                []
              }
            </View>
          </View>
        </View>
      </View>
    );
  }
};