import * as React from 'react';
import { View,  Image } from '../../../common/elements';
import Spinner from '../../../common/spinner';
import Breadcrum from '../../../common/breadcrum';
import { IUser } from '../../../../models/user/user';
import { IAppUser } from '../../../../models/app-user';
import { IUsers } from '../../../../models/store/users';
import { IHomes } from '../../../../models/store/homes';
import { IBlocks } from '../../../../models/store/blocks';
import { IHome, IMember, IVehicle, IServant, ITicket, IVisitor, IPayment } from '../../../../models/home';
import { IDocument } from '../../../../models/document';
import Toast from  '../../../../util/toast';
import { isNN } from '../../../../util/index';
import Info from './info';
import Members from './members';
import Vehicles from './vehicles';
import Servants from './servants';
import Tickets from './tickets';
import Visitors from './visitors';
import Payments from './payments';
import Documents from './documents';

interface IProps {
  match: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  blocks: IBlocks,
  onLoad: (id: string, sCallback: (result: IHome) => void, fCallback: (message: string) => void) => void,
  onLoadMembers: (homeId: string, sCallback: (result: Array<IMember>) => void, fCallback: (message: string) => void) => void,
  onLoadVehicles: (homeId: string, sCallback: (result: Array<IVehicle>) => void, fCallback: (message: string) => void) => void,
  onLoadServants: (homeId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => void,
  onLoadTickets: (homeId: string, sCallback: (result: Array<ITicket>) => void, fCallback: (message: string) => void) => void,
  onLoadVisitors: (homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void,
  onLoadPayments: (homeId: string, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => void,
  onLoadDocuments: (homeId: string, sCallback: (result: Array<IDocument>) => void, fCallback: (message: string) => void) => void
};

interface IState{
  isLoading: boolean,
  homeId: string,
  activeTab: number,
  data: IHome
};

export default class ViewHome extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      homeId: '',
      activeTab: 1,
      data: {} as IHome
    }
    this.toggleLoading = this.toggleLoading.bind(this);
  };

  componentDidMount(){
    let homeId = this.props.match.params.id; // this.props.navigation.state.params.id;
    this.setState({
      homeId: homeId
    });
    if(homeId.length > 0){
      this.setState({
        isLoading: true
      });
      this.props.onLoad(homeId, (result: IHome) => {
        this.setState({
          isLoading: false
        });
        if(result && isNN(result._id)){
          this.setState({
            data: result
          });
        }else{
          Toast.fail('Failed to load Home.');
        }
      }, (message: string) => {
        this.setState({
          isLoading: false
        });
        Toast.fail(isNN(message) ? message : 'Failed to load Home.');
      });
    }
  };

  toggleLoading = (isLoading: boolean) => {
    this.setState({
      isLoading: isLoading
    });
  }

  componentWillUnmount() {}

  render() {
    let { activeTab } = this.state;
    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Homes', link: 'homes'}, {title: 'View Home', link: 'view-home'}]} />
        <View className='bgc-white p-20'>
          <View className='tab-bar'>
            <span className={'tab' + (this.state.activeTab === 1 ? ' active': '')} onClick={() => this.setState({activeTab: 1})}>
              <span className="icon-holder">
                <i className="c-deep-purple-a700 ti-info-alt"></i>
              </span>
            </span>
            <span className={'tab' + (this.state.activeTab === 2 ? ' active': '')} onClick={() => this.setState({activeTab: 2})}>
              <span className="icon-holder">
                <i className="c-brown-500 fa fa-users"></i>
              </span>
            </span>
            <span className={'tab' + (this.state.activeTab === 3 ? ' active': '')} onClick={() => this.setState({activeTab: 3})}>
              <span className="icon-holder">
                <i className="c-indigo-500 fa fa-car"></i>
              </span>
            </span>
            <span className={'tab' + (this.state.activeTab === 4 ? ' active': '')} onClick={() => this.setState({activeTab: 4})}>
              <span className="icon-holder">
                <i className="c-deep-purple-500 fa fa-female"></i>
              </span>
            </span>
            <span className={'tab' + (this.state.activeTab === 5 ? ' active': '')} onClick={() => this.setState({activeTab: 5})}>
              <span className="icon-holder">
                <i className="c-orange-500 ti-headphone-alt"></i>
              </span>
            </span>
            <span className={'tab' + (this.state.activeTab === 6 ? ' active': '')} onClick={() => this.setState({activeTab: 6})}>
              <span className="icon-holder">
                <i className="c-pink-500 fa fa-id-card-o"></i>
              </span>
            </span>
            <span className={'tab' + (this.state.activeTab === 7 ? ' active': '')} onClick={() => this.setState({activeTab: 7})}>
              <span className="icon-holder">
                <i className="c-indigo-500 fa fa-credit-card"></i>
              </span>
            </span>
            <span className={'tab' + (this.state.activeTab === 8 ? ' active': '')} onClick={() => this.setState({activeTab: 8})}>
              <span className="icon-holder">
                <i className="c-blue-500 fa fa-file-text"></i>
              </span>
            </span>
          </View>
          <View className='p-15'>
            {
              activeTab === 1 ?
                  <Info blocks={this.props.blocks} data={this.state.data} />
              :
                activeTab === 2 ?
                  <Members homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadMembers} />
                :
                  activeTab === 3 ?
                    <Vehicles homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadVehicles} />
                  :
                    activeTab === 4 ?
                      <Servants homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadServants} />
                    :
                      activeTab === 5 ?
                        <Tickets homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadTickets} />
                      :
                        activeTab === 6 ?
                          <Visitors appUser={this.props.appUser} user={this.props.user} homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadVisitors} />
                        :
                          activeTab === 7 ?
                            <Payments homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadPayments} />
                          :
                            <Documents homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadDocuments} />
            }            
          </View>
        </View>
        <Spinner visible={this.state.isLoading} />
      </View>
      </View>
      </View>
    );
  }
};